@import 'varibles'
@import 'animations'

body
    margin: 0
    padding: 0
    height: 100vh
    background-image: linear-gradient(to right top, #000000, #11070c, #1a0e18, #1e1422, #1f1a2f, #202137, #21283f, #222f46, #2c3749, #37404c, #42484e, #4f5051)

.container-navbar
    width: 60px
    height: 100vh
 
    display: flex
    flex-direction: column
    justify-content: center
    gap: 50px
    transition: width 0.6s ease
    background: #00000052
    overflow: hidden

    &:hover
        width: 190px

    .navbar
        position: relative
        color: #fff
        cursor: pointer
        display: flex
        align-items: center
        white-space: nowrap

        left: 20px
        transition: transform 0.6s ease, opacity 0.6s ease


        &:hover
            transform: scale(1.1) 
            transition: 0.2s

        &:hover::before
            content: ''
            position: absolute
            left: -10px
            width:88%
            height: 40px
            background: #7a0808
            border-radius: 50px
            
                   
        .fa-solid
            font-size: 1.3em
            transform: translateX(0)
            transition: transform 0.6s ease

        a
            margin-left: 10px
            text-decoration: none
            color: #fff
            font-family: $fontBebas
            font-size: 1.2em
            opacity: 0
            transform: translateX(-20px)
            transition: opacity 0.6s ease, transform 0.6s ease

    &:hover .navbar a
        opacity: 1
        transform: translateX(10%)

    &:hover .navbar .fa-solid
        transform: translateX(0)
